"use client"
import React, { useState } from 'react'
import { useRouter, useSearchParams } from 'next/navigation'
import API from '@/lib/api'
import ImageUploader from '@/components/ImageUploader'

export default function CreateEventPage() {
  const router = useRouter()
  const searchParams = useSearchParams()
  const groupId = searchParams.get('groupId') || ''

  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [tags, setTags] = useState('')
  const [startsAt, setStartsAt] = useState('')
  const [endsAt, setEndsAt] = useState('')
  const [city, setCity] = useState('')
  const [venue, setVenue] = useState('')
  const [address, setAddress] = useState('')
  const [capacity, setCapacity] = useState<number | ''>('')
  const [imageUrl, setImageUrl] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const submit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError(null)
    const token = localStorage.getItem('token')
    if (!token) {
      setError('Please login first')
      setLoading(false)
      return
    }
    try {
      // For now, include tags inline in description if backend lacks a tags field
  const tagsArray = tags.split(',').map(t => t.trim()).filter(Boolean)
  const payload: any = { groupId, title, description, startsAt, city, venue, tags: tagsArray }
      if (endsAt) payload.endsAt = endsAt
      if (address) payload.address = address
      if (capacity !== '') payload.capacity = Number(capacity)
      if (imageUrl) payload.imageUrl = imageUrl

      const res = await API.post('/events', payload)
      router.push(`/events/${res.data.event.id}`)
    } catch (err: any) {
      setError(err?.response?.data?.error || 'Failed to create event')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-pink-50 via-purple-50 to-indigo-50 p-4">
      <div className="w-full max-w-2xl rounded-3xl shadow-xl bg-white ring-1 ring-zinc-200 p-8 md:p-10">
        <div className="flex items-center gap-3 mb-6">
          <span className="inline-flex items-center justify-center rounded-xl bg-gradient-to-br from-indigo-500 to-pink-500 p-3">
            <svg className="h-7 w-7 text-white" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2"><rect x="3" y="4" width="18" height="18" rx="2" ry="2"/><line x1="16" y1="2" x2="16" y2="6"/><line x1="8" y1="2" x2="8" y2="6"/><line x1="3" y1="10" x2="21" y2="10"/></svg>
          </span>
          <div>
            <h2 className="text-2xl font-bold text-zinc-900">Create an Event</h2>
            <p className="text-sm text-zinc-500">Host something amazing for your community</p>
          </div>
        </div>
        <form onSubmit={submit} className="space-y-5">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
            <div>
              <label className="mb-1.5 block text-sm font-medium text-zinc-700">Group ID</label>
              <input value={groupId} readOnly className="w-full rounded-xl border border-zinc-200 bg-zinc-50 py-3 px-4 text-zinc-900" />
            </div>
            <div>
              <label className="mb-1.5 block text-sm font-medium text-zinc-700">Event Title</label>
              <input value={title} onChange={(e) => setTitle(e.target.value)} placeholder="Hackathon Night" className="w-full rounded-xl border border-zinc-200 bg-white py-3 px-4 text-zinc-900 placeholder:text-zinc-400 focus:ring-2 focus:ring-indigo-200" required />
            </div>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
            <div>
              <label className="mb-1.5 block text-sm font-medium text-zinc-700">Starts At</label>
              <input type="datetime-local" value={startsAt} onChange={(e) => setStartsAt(e.target.value)} className="w-full rounded-xl border border-zinc-200 bg-white py-3 px-4 text-zinc-900" required />
            </div>
            <div>
              <label className="mb-1.5 block text-sm font-medium text-zinc-700">Ends At</label>
              <input type="datetime-local" value={endsAt} onChange={(e) => setEndsAt(e.target.value)} className="w-full rounded-xl border border-zinc-200 bg-white py-3 px-4 text-zinc-900" />
            </div>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
            <div>
              <label className="mb-1.5 block text-sm font-medium text-zinc-700">City</label>
              <input value={city} onChange={(e) => setCity(e.target.value)} placeholder="City" className="w-full rounded-xl border border-zinc-200 bg-white py-3 px-4 text-zinc-900 placeholder:text-zinc-400" required />
            </div>
            <div>
              <label className="mb-1.5 block text-sm font-medium text-zinc-700">Venue</label>
              <input value={venue} onChange={(e) => setVenue(e.target.value)} placeholder="Venue" className="w-full rounded-xl border border-zinc-200 bg-white py-3 px-4 text-zinc-900 placeholder:text-zinc-400" />
            </div>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
            <div>
              <label className="mb-1.5 block text-sm font-medium text-zinc-700">Address</label>
              <input value={address} onChange={(e) => setAddress(e.target.value)} placeholder="Address" className="w-full rounded-xl border border-zinc-200 bg-white py-3 px-4 text-zinc-900 placeholder:text-zinc-400" />
            </div>
            <div>
              <label className="mb-1.5 block text-sm font-medium text-zinc-700">Capacity</label>
              <input type="number" value={capacity} onChange={(e) => setCapacity(e.target.value ? Number(e.target.value) : '')} placeholder="Capacity" className="w-full rounded-xl border border-zinc-200 bg-white py-3 px-4 text-zinc-900 placeholder:text-zinc-400" />
            </div>
          </div>
          <div>
            <label className="mb-1.5 block text-sm font-medium text-zinc-700">Tags (comma separated)</label>
            <input value={tags} onChange={(e) => setTags(e.target.value)} placeholder="e.g. tech, networking, fun" className="w-full rounded-xl border border-zinc-200 bg-white py-3 px-4 text-zinc-900 placeholder:text-zinc-400" />
          </div>
          <div>
            <label className="mb-1.5 block text-sm font-medium text-zinc-700">Event Poster</label>
            <ImageUploader value={imageUrl} onChange={setImageUrl} />
          </div>
          <button disabled={loading} className="w-full rounded-xl bg-gradient-to-r from-indigo-500 to-pink-500 py-3 text-white font-semibold shadow hover:from-indigo-600 hover:to-pink-600 disabled:opacity-50">
            {loading ? 'Creating...' : 'Create Event'}
          </button>
          {error && <div className="rounded-lg border border-rose-200 bg-rose-50 p-3 text-sm text-rose-700">{error}</div>}
        </form>
      </div>
    </div>
  )
}
